import React, { useState } from "react";
import { EyeClosed, Lock, Eye } from "lucide-react";
import { useNavigate } from "react-router-dom";
import { useSearchParams } from "react-router-dom";
import { resetPassword } from "../../services/authService";
function ForgetPassword() {
  const [param] = useSearchParams();
  const resetToken = param.get("token");
  const [message, setMessage] = useState({
    password: "",
  });
  const navigate = useNavigate();
  const [show, setShow] = useState(false);
  const [password, setPassword] = useState("");
  async function handleResetPassword(e) {
    e.preventDefault();
    try{
      await resetPassword(resetToken, password);
      navigate("/home",{state:{
        show: true,
        message: "t li mt khu thnh cng!",
        color:"#11224E"
      }});
    }
    catch(err){
      if (err.response) {
        if (err.response.status === 400) {
          const listError = err.response.data.errors;
          const msg = {};
          listError.forEach((error) => {
            msg[error.param] = error.msg;
          });
          setMessage(msg);
        } 
      }
    }
  }
  return (
    <div className="container-login">
      <div className="login-form" style={{ height: "400px", width: "420px" }}>
        <div className="header-login">
          <div
            style={{
              display: "flex",
              width: "60px",
              height: "60px",
              background: "#ff6a00",
              borderRadius: "0.8rem",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <span
              style={{ color: "white", fontWeight: "700", fontSize: "1.4rem" }}
            >
              M
            </span>
          </div>
          <span style={{ fontSize: "22px", marginTop: "20px" }}>
            t li mt khu
          </span>
          <span style={{ fontSize: "15px", marginTop: "5px", opacity: "0.7" }}>
            Nhp mt khu mi ca bn
          </span>
        </div>
        <div className="form-login">
          <span style={{ fontWeight: "500", fontSize: "14px" }}>
            Mt khu mi
          </span>
          <form className="input-group position-relative">
            <input
              type={show ? "text" : "password"}
              value={password}
              className="form-control"
              placeholder="Nhp mt khu"
              style={{
                paddingLeft: "2.5rem",
                borderRadius: "0.5rem",
                fontSize: "14px",
              }}
              onChange={(e) => {
                setPassword(e.target.value);
              }}
            />
            <Lock className="search-icon" />
            <div
              onClick={() => {
                setShow(!show);
              }}
            >
              {show ? (
                <Eye className="eye-icon" />
              ) : (
                <EyeClosed className="eye-icon" />
              )}
            </div>
          </form>
          <span style={{ color: "red" }}>{message.password}</span>
        </div>
        <div
          className="btn-create"
          style={{ width: "100%", marginTop: "20px" }}
          onClick={() => {
            handleResetPassword(event);
            
          }}
        >
          <span style={{ fontWeight: "500" }}>Xc nhn</span>
        </div>
      </div>
    </div>
  );
}
export default ForgetPassword;
