import React from "react";
import { useState } from "react";
import TagItem from "./TagItem";
function PopularTag(props) {
  const tag = props.tag;
  const listTag = [
    { name: "Chi game", selected: false },
    { name: "Hc tp", selected: false },
    { name: "Lm vic vn phng", selected: false },
    { name: "Thit k  ha", selected: false },
    { name: "Lp trnh", selected: false },
    { name: "Chp nh & Quay video", selected: false },
    { name: "Gii tr & Xem phim", selected: false },
    { name: "Nghe nhc", selected: false },
    { name: "Sc khe & Th thao", selected: false },
    { name: "Theo di gic ng", selected: false },
    { name: "Di chuyn & Du lch", selected: false },
    { name: "Nh thng minh", selected: false },
    { name: "Ph huynh & Tr em", selected: false },
    { name: "Ngi ln tui", selected: false },
    { name: "Dng hng ngy", selected: false },
  ];

  const [tags, setTags] = useState(
    listTag.map((item) => ({
      ...item,
      selected: tag.includes(item.name),
    }))
  );
  function handleTag(name) {
    const index = tag.indexOf(name);
    if (index !== -1) {
      tag.splice(index, 1);
    } else {
      tag.push(name);
    }
    setTags((prev) =>
      prev.map((tag) =>
        tag.name === name ? { ...tag, selected: !tag.selected } : tag
      )
    );
    props.onTag({ keyword: tag });
  }
  return (
    <div className="categories-container" style={{ marginTop: "20px" }}>
      <span
        style={{
          fontWeight: "500",
          marginBottom: "10px",
        }}
      >
        T kha ph bin
      </span>
      <div className="tag-container">
        {tags.map((tag, index) => {
          return (
            <TagItem
              key={index}
              name={tag.name}
              selected={tag.selected}
              onSelect={handleTag}
            />
          );
        })}
      </div>
    </div>
  );
}
export default PopularTag;
